@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700&display=swap);
/* https://fonts.google.com/specimen/Mulish#standard-styles */

body {
  font-family: 'Mulish', sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 24px;
}

p {
  font-size: 16px;
}

ul {
  list-style: none;
  padding: 0;
}

/* block__element--modifier */

.App {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.Directions__list li {
  padding-bottom: 8px;
}

.Footer {
  align-items: center;
  background-color: #7b68a1;
  color: whitesmoke;
  display: flex;
  font-size: 12px;
  height: 128px;
  justify-content: center;
  width: 100%;
}

.Header {
  align-items: center;
  background-color: #f7972e;
  display: flex;
  font-family: 'Courier New', Courier, monospace;
  font-size: 32px;
  font-weight: bold;
  height: 64px;
  justify-content: center;
  width: 100%;
}

.Header a {
  color: whitesmoke;
  text-decoration: none;
}

.Heading__small {
  color: gray;
  font-size: 16px;
  margin: 0 0 8px 4px;
}

.Ingredients__list {
  list-style: disc;
  margin-left: 24px;
}

.Ingredients__list li {
  padding-bottom: 4px;
}

.Meta li {
  padding-bottom: 4px;
}

.Nav {
  max-width: 425px;
  padding: 0 32px;
}

.Nav__hero {
  border: 1px solid lightgray;
  margin-right: 8px;
  width: 25%;
}

.Nav__item {
  display: flex;
  margin: 8px;
}

.Recipe {
  padding: 0 32px;
  max-width: 425px;
}

.Recipe h1 {
  margin: 24px 0 8px;
}

.Recipe__hero {
  border: 1px solid lightgray;
  width: 100%;
}

.Tags {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0 16px 16px;
}

.Tags li {
  font-size: 12px;
  font-weight: bold;
  margin: 2px 8px 2px 0;
  text-decoration: underline;
}

